.breadcrumbs {
  display: inline-block;
  font-family: "PT Sans", sans-serif;
  counter-reset: flag;
  color: black;
  font-size: 1.2rem;
  text-align: center;
  margin: 0 0 1rem; }
  @media only screen and (max-width: 21em) {
    .breadcrumbs {
      font-size: 1rem; } }

.breadcrumbs a {
  position: relative;
  float: left;
  padding-left: 5rem;
  padding-right: 1rem;
  text-decoration: none;
  line-height: 3.6rem;
  color: black;
  background: white;
  transition: all 0.5s; }

.breadcrumbs a::after {
  content: '';
  position: absolute;
  top: 0;
  right: -1.8rem;
  z-index: 1;
  width: 3.6rem;
  height: 3.6rem;
  background: white;
  box-shadow: 1px 1px 0 1px #ddd;
  border-radius: 50px 0 0 0;
  transform: rotate(-45deg) scale(0.73);
  transition: all 0.5s; }

.breadcrumbs a:hover,
.breadcrumbs a:hover::after {
  background: #8BCB46;
  color: #ffffff; }

.breadcrumbs a::before {
  content: counter(flag);
  counter-increment: flag;
  position: absolute;
  top: .8rem;
  left: 2.5rem;
  line-height: 2rem;
  box-shadow: 0 0 2px #ccc;
  width: 2rem;
  background-color: white;
  border-radius: 50%; }

.breadcrumbs a:first-child {
  padding-left: 4rem; }

.breadcrumbs a:first-child::before {
  left: 1.4rem; }

